.test-home {
  .sy-header, .sy-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }

  .sy-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }

  .sy-main {
    background-color: #E9EEF3;
    color: #333;
    /*text-align: center;*/
    /*line-height: 160px;*/
  }

  body > .sy-container {
    margin-bottom: 40px;
  }

  .sy-container:nth-child(5) .sy-aside,
  .sy-container:nth-child(6) .sy-aside {
    line-height: 260px;
  }

  .sy-container:nth-child(7) .sy-aside {
    line-height: 320px;
  }

  .sy-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
}

